import React,{Component} from 'react';
import Home from './Home/Home.js';
import "./App.css";
import Cs from './Home/Cs.js';

class App extends Component {
	constructor(){
    super();
    this.state ={
      route: 'Home'
    }
  }
  onRouteChange = (route) =>{
    this.setState({route: route})
  }
  render() {
  	 const {route} = this.state;
    return(

      <div>
      {route === 'Home' ?
        <Home onRouteChange={this.onRouteChange}/>
        : <Cs onRouteChange={this.onRouteChange}/>}
      </div>
    )
  }
}

export default App;